import React,{lazy,Suspense} from "react";

import { Routes, Route } from "react-router-dom";

import DetailView from "../components/views/detailsView";
import ListaView from "../components/views/listaView";
import SetPrivate, { PublicRoute } from "./privateRoutes";
import Login2 from '../components/views/login'


let rutas = ()=> {
  return (
    <Routes>
      <Route
        path="star-wars-app/"
        element={
          <PublicRoute>
            <Login2 />
          </PublicRoute>
        }
      />

      <Route
        path="/star-wars-app/listaView"
        element={
          <SetPrivate>
            <ListaView />
          </SetPrivate>
        }
      ></Route>

      <Route
        path="/star-wars-app/detailView/:id"
        element={
          <SetPrivate>
            <DetailView />
          </SetPrivate>
        }
      />

      <Route path="*" element={"Que haces aqui"} />
    </Routes>
  );
}

export default rutas